/* Quick view button */
.quickview-wrapper {
    display: inline-block;
    text-align: center;

    .spc-product-quickview-btn {
        background: rgba(255, 255, 255, 0.2);
        padding: 10px 10px;
        width: 100%;
        color: #fff;
        display: inline-block;
        box-sizing: border-box;
        line-height: 1;

        .spm-search-icon {

            svg {
                margin-bottom: -5px;
            }
        }
    }
}


/* Quick view */
#spc-viewmodal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
    opacity: 0;
    visibility: hidden;
    background-color: transparent;
    overflow-y: auto;
}

#spc-viewmodal .spc-modal-loading {
    position: absolute;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    top: 0;
    bottom: 0;
    opacity: 0;
    visibility: hidden;
}

#spc-viewmodal .spc-modal-loading.active {
    opacity: 0.5;
    visibility: visible;
}

#spc-viewmodal .spc-modal-loading .spc-loader {
    left: 49%;
    top: 50%;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    margin-top: -30px;
    margin-left: -30px;
    position: absolute;
    border: 0.4rem solid rgba(255, 255, 255, 0.2);
    border-top-color: #7a56ff;
    animation: spining 1s infinite linear;
}

@keyframes spining {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

#spc-viewmodal.spcquickview-open {
    opacity: 1;
    visibility: visible;
    background-color: rgba(0, 0, 0, 0.75);
}

#spc-viewmodal .spc-close-btn {
    background: transparent none repeat scroll 0 0;
    color: #000;
    height: inherit;
    line-height: inherit;
    padding: 0;
    position: absolute;
    right: 5px;
    top: 5px;
    cursor: pointer;
    display: none;
    border-radius: 50%;
    border: none;
}

#spc-viewmodal .spc-close-btn:hover {
    color: red;
    background: transparent;
}

#spc-viewmodal .spc-modal-quickview-dialog {
    max-width: 960px;
    padding: 35px;
    width: 100%;
    position: relative;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease 0s;
    border-radius: 10px;
}

#spc-viewmodal.spcquickview-open .spc-modal-quickview-dialog {
    opacity: 1;
    visibility: visible;
    transition: all 0.3s ease 0.3s
}

#spc-viewmodal.woocommerce div.product .product_title {
