.spc-woo-product-inner-wrap {

    .spc-product-cat .spc-product-taxonomy {
        font-size: inherit;
        line-height: inherit;
        width: inherit;
        height: inherit;
        margin-top: 12px;
        margin-bottom: 8px;
        display: inline-block;

        a {
            position: relative;
            display: inline-block;
            word-break: break-all;
            z-index: 10;
            font-size: 11px;
            line-height: 1;
            font-weight: 600;
            margin-top: 0px;
            margin-bottom: 0px;
            z-index: 10;
            color: rgba(0, 0, 0, 0.5);
            text-transform: capitalize;
            letter-spacing: 1px;
            padding: 4px 10px;
            border: 1px solid rgba(0, 0, 0, 0.15);
            border-radius: 48px;
        }
    }

}

.spc-woo-product-wrapper {
    .spc-filterable-menu {
        padding: 0px 30px 0px 30px;
        margin: 0px 0px 30px 0px;
        background-color: #FFFFFF;
        text-align: center;
        display: flex;

        .filter-item:not(:last-child) {
            margin-right: 10px;
        }

        .filter-item {
            background: none;
            border: none;
            cursor: pointer;
            outline: none;
            transition: all .3s ease;
            text-transform: capitalize;
            display: inline-flex;
            padding: 20px 20px 20px 20px;
        }

        .filter-item.current {
            color: #7a56ff;
            border-style: solid;
            border-width: 0px 0px 2px 0px;
            border-color: #7a56ff;
        }

        .filter-item .cat-count {
            color: #FFFFFF;
            background: #7a56ff;
            padding: 0px 5px;
            margin-left: 5px;
            border-radius: 50px;
            text-align: center;
            width: 25px;
            height: 25px;
            line-height: 25px;
            font-size: 12px;

        }

    }

}