.spc-product-rating {
    line-height: 2;
    display: block;

    &::before,
    &::after {
        content: " ";
        display: table;
    }

    &::after {
        clear: both;
    }

    .star-rating {
        font-size: 20px;
        margin: 0px 0px 0px 0px;
        color: #FF5656;
        float: left;
        overflow: hidden;
        position: relative;
        height: 1em;
        line-height: 1;
        font-size: 1em;
        width: 5.4em;
        font-family: star;

        &::before {
            content: "sssss";
            color: #d3ced2;
            float: left;
            top: 0;
            left: 0;
            position: absolute;
        }

        span {
            overflow: hidden;
            float: left;
            top: 0;
            left: 0;
            position: absolute;
            padding-top: 1.5em;

            &:before {
                content: "SSSSS";
                top: 0;
                position: absolute;
                left: 0;
            }
        }

    }
}