.spc-woo-product-inner-wrap {
    position: relative;

    .spc-product-tag {

        z-index: 11;
    }

    .spc-product-tag {
        position: absolute;
        top: 20px;
        margin: 0;
        color: #ffffff;
        background: #3BC473;
        border-radius: 50px 50px 50px 50px;
        padding: 5px 15px;
        color: #fff;
        font-size: 14px;
        display: inline-block;
        border-color: #3BC473;
    }

    .spc-discount-price {
        position: absolute;
        left: auto;
        right: 20px;
    }

    .spc-stock-status {
        left: 20px;
        top: 20px;

        .spc-product-outofstock {
            background: #FF5656;
            border-color: #FF5656;
        }
    }

    .spc-featured-product {
        position: absolute;
        top: 20px;
        left: 120px;
        background: #FF5656;
        border-color: #FF5656;
    }
}