.spc-product-image {
    position: relative;

    .product-img {
        display: block;
    }

    .quickview-wrapper {
        position: absolute;
        top: auto;
        bottom: 20px;
        left: auto;
        right: auto;
        display: inline-block;

        .spc-product-quickview-btn {
            width: 40px;
            height: 40px;
            line-height: 40px;

            svg {
                margin-bottom: -4px;
            }
        }
    }
}